.up-link {
  width: 64px;
  height: 64px;
  color: #303030;
  background-color: #f8aaed;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  transition: background-color .25s linear;
  display: flex;
  position: fixed;
  bottom: 24px;
  right: 24px;
}

.up-link:hover, .up-link:focus {
  background-color: #27ae60;
}

.gallery-section {
  padding: 60px 0;
}

.search-form {
  width: 600px;
  justify-content: center;
  margin: 0 auto 40px;
  display: flex;
}

.search-input {
  height: 40px;
  border: 1px solid #303030;
  border-radius: 5px;
  flex-grow: 1;
  padding-left: 15px;
  font-size: 18px;
}

.search-btn, .load-more {
  color: #3f3f3f;
  text-transform: uppercase;
  cursor: pointer;
  letter-spacing: .5px;
  background-color: #f8aaed;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 700;
  transition: background-color .25s linear;
}

.load-more {
  margin: 0 auto;
  display: block;
}

.load-more.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.search-btn:hover, .search-btn:focus, .load-more:hover, .load-more:focus {
  color: #3f3f3f;
  background-color: #27ae60;
}

.gallery {
  flex-wrap: wrap;
  column-gap: 30px;
  margin-bottom: 20px;
  display: flex;
}

.gallery__item, .photo-card {
  border-radius: 5px;
  flex-direction: column;
  flex-basis: calc(33.3333% - 20px);
  justify-content: space-between;
  margin-bottom: 30px;
  display: flex;
  overflow: hidden;
  box-shadow: 0 3px 8px rgba(0, 0, 0, .5);
}

.gallery-img {
  transition: transform .25s linear;
}

.gallery-img:hover {
  object-fit: cover;
  transform: scale(1.08);
}

.info {
  border-top: rgba(0, 0, 0, 0);
  flex-grow: 1;
  justify-content: space-between;
  align-items: flex-end;
  padding: 20px;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.disabled, .disabled:hover {
  color: #303030;
  opacity: .8;
  background-color: #ccc;
}

